import React, { useEffect, useState } from 'react'
import { styled } from '@mui/material/styles'
import moment from 'moment'
import { BTitle } from 'components/CommonComponent'
import Tabs from 'components/Tabs'
import Card from 'components/Card'
import { Button } from 'components/uikit'
import Table, { StyledTableCell } from 'components/Table'

window.moment = moment

const Box = styled('div')`
  box-sizing: border-box;
  margin-top: 26px;
  padding-right: 44px;
  height: 80%;
  overflow: scroll;
  padding-bottom: 20px;

  &::-webkit-scrollbar {
    display: none;
  }
`

export const ClaimButton = styled(Button)`
  background: linear-gradient(90deg, #61c2f7 0%, #7ae9f0 100%);
  border-radius: 20px;
  box-shadow: none;
  height: 30px;
  line-height: 30px;
  color: #000;
  font-size: 14px;
  display: block;
  margin: 30px auto 0;
`

const TableCard = styled(Card)`
  margin-bottom: 20px;
  padding: 8px;
`

const AvatarColumns = [
  {
    id: 'date',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Date',
  },
  {
    id: 'tokenSymbol',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  {
    id: 'avatarRewards',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Claimed Rewards',
  },
  {
    id: 'type',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'account',
  },
]
const WalletColumns = [
  {
    id: 'date',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Date',
  },
  {
    id: 'tokenSymbol',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  {
    id: 'walletRewards',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Claimed Rewards',
  },
  {
    id: 'address',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Account',
  },
]
const WalletList = ({ list }) => {
  return (
    <TableCard>
      <Table
        dataSource={list.filter((it) => it.type === 'My Wallet')}
        columns={WalletColumns}
        pagiation={false}
        content={(row, index, labelId) => (
          <>
            <StyledTableCell component="th" id={labelId} scope="row">
              {moment(row.date).format('YYYY-MM-DD HH:mm')}
            </StyledTableCell>
            <StyledTableCell>{row.tokenSymbol}</StyledTableCell>
            <StyledTableCell>{row.walletRewards} WBTC</StyledTableCell>
            <StyledTableCell>{row.type}</StyledTableCell>
          </>
        )}
      />
    </TableCard>
  )
}

const AvatarList = ({ list }) => {
  return (
    <TableCard>
      <Table
        dataSource={list.filter((it) => it.type === 'Avatar')}
        columns={AvatarColumns}
        pagiation={false}
        content={(row, index, labelId) => (
          <>
            <StyledTableCell component="th" id={labelId} scope="row">
              {moment(row.date).format('YYYY-MM-DD HH:mm')}
            </StyledTableCell>
            <StyledTableCell>{row.tokenSymbol}</StyledTableCell>
            <StyledTableCell>{row.avatarRewards} WBTC</StyledTableCell>
            <StyledTableCell>Collateral</StyledTableCell>
          </>
        )}
      />
    </TableCard>
  )
}
const AllList = ({ list }) => {
  return (
    <>
      <TableCard>
        <Table
          dataSource={list}
          columns={WalletColumns}
          pagiation={false}
          content={(row, index, labelId) => (
            <>
              <StyledTableCell component="th" id={labelId} scope="row">
                {moment(row.date).format('YYYY-MM-DD HH:mm')}
              </StyledTableCell>
              <StyledTableCell>{row.tokenSymbol}</StyledTableCell>
              <StyledTableCell>{row.type === 'Avatar' ? row.avatarRewards : row.walletRewards} WBTC</StyledTableCell>
              <StyledTableCell>{row.type === 'Avatar' ? 'Collateral' : row.type}</StyledTableCell>
            </>
          )}
        />
      </TableCard>
    </>
  )
}

function ClaimedHistory(props) {
  const { match } = props
  const [dataSource, setDataSource] = useState([])
  const list = window.localStorage.getItem('claimRewardsList')

  useEffect(() => {
    if (list) {
      const _list = JSON.parse(list)
      setDataSource(_list)
    }
  }, [list])

  return (
    <>
      <BTitle>Claimed Rewards History</BTitle>
      <Box>
        <Tabs
          tabsStyle={{ marginTop: 0 }}
          defaultTabsValue={match.params.id}
          tab={[
            {
              label: 'All',
              component: <AllList list={dataSource} />,
            },
            {
              label: 'My Wallet',
              component: <WalletList list={dataSource} />,
            },
            {
              label: 'Collateral',
              component: <AvatarList list={dataSource} />,
            },
          ]}
        />
      </Box>
    </>
  )
}

export default ClaimedHistory
